.katakana,
.romaji {
  font-family: "ＭＳ Ｐゴシック", Osaka, "ヒラギノ角ゴ Pro W3";
  text-align: center;
  line-height: 150%;
}

.katakana {
  font-size: 1.75rem;
  font-weight: bold;
  text-align: center;

  margin-top: 0.75rem;
}

.romaji {
  font-size: 1rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;

  margin-top: 0.5rem;
}

@media (min-width: 30em) {

  .katakana {
    font-size: 2.5rem;
    margin-top: 1.5rem;
  }

  .romaji {
    font-size: 1.4rem;
    letter-spacing: 0.25rem;
    margin-top: 1rem;
  }
}
